import { RendererCanvas } from '../../../../lib';

export class Solution extends RendererCanvas {
    divText: HTMLDivElement;
    divSel: HTMLDivElement;
    graphController: GraphController;


    constructor(id: string) {
        super(document.getElementById(id) as HTMLDivElement);

        this.canvas.style.position = 'absolute'
        const divMenu = document.createElement('div');
        divMenu.style.position = 'absolute'
        this.divHost.appendChild(divMenu);

        const divBtn = document.createElement('button');
        divBtn.textContent = 'run'
        divBtn.onclick = () => {
            this.graphController.needPopulate = !this.graphController.needPopulate;
        }
        divMenu.appendChild(divBtn);

        const divRemove = document.createElement('button');
        divRemove.textContent = 'remove'
        divRemove.onclick = () => {
            this.graphController.removeSelection();
        }
        divMenu.appendChild(divRemove);

        const divText = document.createElement('div');
        divText.textContent = 'N:0, E:0'
        divMenu.appendChild(divText);
        this.divText = divText;

        const divSel = document.createElement('div');
        divSel.textContent = 'undefined'
        divMenu.appendChild(divSel);
        this.divSel = divSel;

        const btnExport = document.createElement('button');
        btnExport.textContent = 'Export JSON';
        divMenu.appendChild(btnExport);
        btnExport.onclick = () => {
            this.graphController.exportJSON();
        }

        const divDes = document.createElement('div');
        divDes.innerHTML = '1 Export graph to JSON <br> 2 display the order of nodes ';
        divMenu.appendChild(divDes);

        this.init();
    }
    public init() {
        // TODO
        this.graphController = new GraphController(this.canvas);
        this.graphController.divLog = this.divText;
        this.graphController.divSel = this.divSel;
        this.mouseClick(200, 200);

        this.start()
    }

    public render(ctx: CanvasRenderingContext2D): void {
        this.graphController.render(ctx);
    }
    public mouseClick(x: number, y: number): void {


        this.graphController.mouseClick(x, y);
    }
    public mouseDown(x: number, y: number): void {
        this.graphController.mouseDown(x, y)
    }
    public mouseMove(x: number, y: number): void {
        this.graphController.mouseMove(x, y);
    }
    public mouseDrag(x: number, y: number): void {
        this.graphController.mouseDrag(x, y)
    }
    public mouseUp() {
        this.graphController.mouseUp();
    }
}


class GraphController {
    public graph: Graph;
    public canvas: HTMLCanvasElement;
    public divLog: HTMLDivElement;
    public divSel: HTMLDivElement;

    public needPopulate = false;
    public m = new Vector3(0, 0, 0);

    private nodePre: Node | Edge | undefined = undefined

    constructor(canvas: HTMLCanvasElement) {
        this.graph = new Graph();
        this.canvas = canvas;
    }
    public populate() {
        const x = Math.random() * this.canvas.width;
        const y = Math.random() * this.canvas.height;
        this.mouseClick(x, y);
    }
    public removeSelection() {
        // console.log(this.graph.captured)
        if (this.graph.captured) {
            this.graph.captured.remove();
            this.graph.captured = undefined;
        }
    }
    // ..........................................................
    public mouseDown(x: number, y: number): void {
        const node = this.graph.getNodeVec(x, y);

        if (this.graph.captured && this.graph.captured !== node) {
            this.nodePre = this.graph.captured;
        };

        this.graph.captured = node
    }

    public mouseClick(x: number, y: number): void {

        if (this.nodePre && this.graph.captured) {
            // console.log('connection needed')
            this.graph.addEdge(this.nodePre as Node, this.graph.captured as Node)
            return;
        }



        const closeEdge = this.graph.getEdgeVec(x, y);

        // connect selected nodes


        // add node by edge
        if (closeEdge && this.graph.captured === undefined) {
            const node = this.graph.addNode(closeEdge.vec.x, closeEdge.vec.y);
            this.graph.edgeSplitByNode(node, closeEdge.edge);
            return
        }

        if (this.graph.captured === undefined) {
            // console.log(this.graph.captured)
            const node = this.graph.addNode(x, y, 0);
            this.graph.addEdgeClosestByNode(node);
        }
    }

    public mouseMove(x: number, y: number): void {
        this.m.x = x;
        this.m.y = y;
    }
    public mouseUp() {
        if (this.graph.captured && this.graph.captured.type === 'node') {
            const node = this.graph.captured as Node;

            node.edges.forEach((e, i) => {
                this.graph.edgeSplitByEdge(e)
            })

            const other = this.graph.getNode(node);
            if (other) {
                const dis = distance(node.vec, other.vec);
                if (dis < 8) {
                    this.graph.mergeTwoNodes(other, node);
                }
            }
        }
    }
    public mouseDrag(x: number, y: number): void {
        if (this.graph.captured && this.graph.captured.type === 'node') {
            const node = this.graph.captured as Node;
            node.vec.x = x;
            node.vec.y = y;
        }
    }
    public render(ctx: CanvasRenderingContext2D) {
        if (this.needPopulate) {
            this.populate();
        }

        this.graph.render(ctx);

        this.divLog.textContent = this.graph.log;
        // this.divSel.textContent = JSON.stringify(this.graph.captured);

        if (this.graph.captured) {
            this.divSel.textContent = this.graph.captured.type;
        } else {
            this.divSel.textContent = 'undefined';
        }

        ctx.beginPath();
        ctx.arc(this.m.x, this.m.y, 2, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fillStyle = '#888';
        ctx.fill();


    }
    public exportJSON() {
        const data = this.graph.toJSON();
        downloadJson(JSON.stringify(data), 'graph.json')
    }
}




class Graph {
    public log: string = '';
    public nodes: Node[] = [];
    public edges: Edge[] = [];

    private _captured: Node | Edge | undefined = undefined;
    constructor() {

    }
    public updatePost() {
        // this.divLog.textContent = `N:${this.nodes.length}, E:${this.edges.length}`;
    }
    public get captured() {
        return this._captured
    }
    public set captured(v) {
        if (this._captured) {
            this._captured.isSelected = false;
        }

        this._captured = v;
        if (v) {
            this._captured.isSelected = true;
        }

    }
    // ..............................................................
    public addNode(x: number, y: number, z: number = 0) {
        // console.trace()
        const node = new Node(this, x, y, 0);
        node.id = this.nodes.length;
        this.nodes.push(node);
        this.updatePost();
        return node;
    }
    public addEdgeClosestByNode(node: Node) {
        const nn = this.getNode(node);
        if (nn) this.addEdge(node, nn);
    }
    public addEdge(n0: Node, n1: Node) {
        const edge = new Edge(n0, n1);
        n0.edges.push(edge);
        n1.edges.push(edge);
        this.edges.push(edge);

        this.edgeSplitByEdge(edge);
        this.updatePost();
        return edge;
    }
    public addEdgeSlipt(n0: Node, n1: Node) {
        const edge = new Edge(n0, n1);
        n0.edges.push(edge);
        n1.edges.push(edge);
        this.edges.push(edge);
        return edge;
    }
    // ..............................................................
    public edgeSplitByNode(node: Node, edge: Edge) {
        const n0 = edge.n0;
        const n1 = edge.n1;

        this.addEdge(n0, node);
        this.addEdge(node, n1);

        this.removeEdge(edge);
    }
    public edgeSplitByEdge(edge: Edge) {
        const nodes: Node[] = [];
        for (let i = this.edges.length - 1; i > -1; --i) {
            const other = this.edges[i];
            // if (edge === other) continue;
            if (edge.n0 !== other.n0 && edge.n0 !== other.n1 && edge.n1 !== other.n0 && edge.n1 !== other.n1) {
                const inter = getIntersectEdge(edge, other);
                if (inter) {
                    const nn = new Node(this, inter[0], inter[1])
                    this.nodes.push(nn);
                    this.addEdgeSlipt(nn, other.n0);
                    this.addEdgeSlipt(nn, other.n1);
                    this.removeEdge(other);
                    nodes.push(nn);
                }
            }
        }
        // console.log('dd', nodes.length);
        if (nodes.length > 0) {
            let nodeFirst = edge.n0;
            nodes.push(edge.n1);
            const disList = nodes.map((n) => { return distance(n.vec, edge.n0.vec); });
            const indices = disList.map((val, index) => index);
            const sortedDis = indices.sort((a, b) => disList[a] - disList[b]);
            this.addEdgeSlipt(nodeFirst, nodes[sortedDis[0]]);
            for (let i = 0; i < sortedDis.length - 1; ++i) {
                this.addEdgeSlipt(nodes[sortedDis[i]], nodes[sortedDis[i + 1]]);
            }
            this.removeEdge(edge);
        }
        this.updateNodeID();
    }
    // ..............................................................
    public removeEdge(other) {
        other.n0.removeEdge(other);
        other.n1.removeEdge(other);
        this.edges.splice(this.edges.indexOf(other), 1);
    }
    public removeNode(node: Node) {
        node.edges.forEach((edge) => {
            if (edge.n0 === node) {
                edge.n1.removeEdge(edge)
            } else {
                edge.n0.removeEdge(edge)
            }
            this.edges.splice(this.edges.indexOf(edge), 1);
        })
        this.nodes.splice(this.nodes.indexOf(node), 1);
        this.updateNodeID();
    }
    // ..............................................................
    public getNode(node: Node) {
        let theDis = Number.MAX_VALUE;
        let index = -1;
        for (let i = 0; i < this.nodes.length; ++i) {
            if (this.nodes[i] === node) continue;
            const vec = this.nodes[i].vec;
            const dis = distance(vec, node.vec)
            if (theDis > dis) {
                theDis = dis;
                index = i;
            }
        }
        if (index > -1) { return this.nodes[index]; }
        return undefined;
    }
    public getNodeVec(x: number, y: number, minDis = 8) {
        let theDis = Number.MAX_VALUE;
        let index = -1;
        const v = new Vector3(x, y)
        for (let i = 0; i < this.nodes.length; ++i) {
            const vec = this.nodes[i].vec;
            const dis = distance(vec, v)
            if (theDis > dis) {
                theDis = dis;
                index = i;
            }
        }
        if (index > -1 && theDis < minDis) { return this.nodes[index]; }
        return undefined;
    }
    public getEdgeVec(x: number, y: number, minDis = 8) {
        const mv = new Vector3(x, y, 0)
        for (let i = 0; i < this.edges.length; ++i) {
            const edge = this.edges[i];
            const cp = pointOnLine(mv, edge.n0.vec, edge.n1.vec)

            const dis = distance(mv, cp);
            if (dis < minDis) {
                return { edge, vec: cp }
            }
        }
        return undefined;
    }
    public getEdgeByTwoNode(n0: Node, n1: Node) {
        for (let i = 0; i < this.edges.length; ++i) {
            const edge = this.edges[i];
            if ((edge.n0 === n0 && edge.n1 === n1) || (edge.n0 === n1 && edge.n1 === n0)) {
                return edge;
            }
        }
        return undefined;
    }
    // ..............................................................
    public mergeTwoNodes(n0: Node, n1: Node) {

        const others = []
        for (let i = 0; i < n1.edges.length; ++i) {
            const edge = n1.edges[i]
            if (edge.n0 === n1) {
                others.push(edge.n1)
            } else {
                others.push(edge.n0)
            }
        }
        n1.remove();

        others.forEach((n) => {
            if (!this.getEdgeByTwoNode(n0, n)) {
                this.addEdge(n0, n);
            }
        })
    }
    public updateNodeID() {
        for (let i = 0; i < this.nodes.length; ++i) {
            this.nodes[i].id = i;
        }
    }
    public toJSON() {
        const data = { node: [], edge: [] }
        this.nodes.forEach((n) => { data.node.push(n.toJSON()); });
        this.edges.forEach((e) => { data.edge.push(e.toJSON()); });
        return data;
    }
    public render(ctx: CanvasRenderingContext2D): void {
        this.log = `N:${this.nodes.length}, E:${this.edges.length}`;
        // console.log(`N:${this.nodes.length}, E:${this.edges.length}`)
        this.edges.forEach((e: Edge) => { e.render(ctx); })
        this.nodes.forEach((n: Node) => { n.render(ctx); })
    }
}

class Edge {
    public type = 'edge';
    public n0: Node;
    public n1: Node;
    public isSelected = false;
    public data: any = [];
    constructor(n0: Node, n1: Node) {
        this.n0 = n0;
        this.n1 = n1;
    }
    public toJSON() {
        return {
            n0: this.n0.id,
            n1: this.n1.id,
            data: this.data,
        };
    }
    public remove() {
        this.n0.removeEdge(this);
        this.n1.removeEdge(this);
    }
    public render(ctx) {
        if (this.isSelected) {
            ctx.lineWidth = 3;
            ctx.strokeStyle = '#00f';
            ctx.beginPath();
            ctx.moveTo(this.n0.vec.x, this.n0.vec.y);
            ctx.lineTo(this.n1.vec.x, this.n1.vec.y);
            ctx.stroke();
        }
        ctx.lineWidth = 1;
        ctx.beginPath();
        ctx.moveTo(this.n0.vec.x, this.n0.vec.y);
        ctx.lineTo(this.n1.vec.x, this.n1.vec.y);
        ctx.strokeStyle = '#0f0';
        ctx.stroke();
    }
}

class Node {
    public type = 'node';
    public graph: Graph;
    public vec: Vector3;
    public r: number = 5;
    public edges: Edge[] = [];
    public isSelected = false;
    public id = -1;
    public data: any = [];
    constructor(graph: Graph, x: number, y: number, z: number = 0) {
        this.graph = graph;
        this.vec = new Vector3(x, y, z);
    }
    public toJSON() {
        return {
            id: this.id,
            vec: { x: this.vec.x, y: this.vec.y, z: this.vec.z },
            data: this.data
        };
    }
    public remove() {
        this.graph.removeNode(this);
    }
    public removeEdge(other) {
        this.edges.splice(this.edges.indexOf(other), 1);
    }
    public render(ctx: CanvasRenderingContext2D) {
        ctx.beginPath();
        ctx.arc(this.vec.x, this.vec.y, this.r, 0, 3.14 * 2)
        ctx.closePath();
        ctx.fillStyle = '#ff0000';
        ctx.fill();
        if (this.isSelected) {
            ctx.lineWidth = 3;
            ctx.strokeStyle = '#00f';
            ctx.stroke();
        }

        ctx.strokeStyle = '#ff0000';
        ctx.lineWidth = 1
        ctx.strokeText(`${this.id}`, this.vec.x, this.vec.y - 7)
    }
}







class Vector3 {
    public x: number;
    public y: number;
    public z: number;
    constructor(x: number, y: number, z: number = 0) {
        this.x = x;
        this.y = y;
        this.z = z
    }
}

const distance = (v0: Vector3, v1: Vector3) => {
    return Math.sqrt(distance2(v0, v1));
}
const distance2 = (v0: Vector3, v1: Vector3) => {
    return (v0.x - v1.x) * (v0.x - v1.x) + (v0.y - v1.y) * (v0.y - v1.y) + (v0.z - v1.z) * (v0.z - v1.z);
}

const getIntersectEdge = (e0: Edge, e1: Edge) => {
    return getIntersect(e0.n0, e0.n1, e1.n0, e1.n1)
}

const getIntersect = (n0: Node, n1: Node, n2: Node, n3: Node) => {
    return getLineIntersectPlane(n0.vec.x, n0.vec.y, n1.vec.x, n1.vec.y, n2.vec.x, n2.vec.y, n3.vec.x, n3.vec.y)
}

const getLineIntersectPlane = (x1: number, y1: number, x2: number, y2: number, x3: number, y3: number, x4: number, y4: number) => {
    // Check if none of the lines are of length 0
    if ((x1 === x2 && y1 === y2) || (x3 === x4 && y3 === y4)) {
        return undefined;
    }

    const denominator = ((y4 - y3) * (x2 - x1) - (x4 - x3) * (y2 - y1));

    // Lines are parallel
    if (denominator === 0) {
        return undefined;
    }

    const ua = ((x4 - x3) * (y1 - y3) - (y4 - y3) * (x1 - x3)) / denominator;
    const ub = ((x2 - x1) * (y1 - y3) - (y2 - y1) * (x1 - x3)) / denominator;

    // is the intersection along the segments
    if (ua < 0 || ua > 1 || ub < 0 || ub > 1) {
        return undefined;
    }

    // Return a object with the x and y coordinates of the intersection
    const x = x1 + ua * (x2 - x1);
    const y = y1 + ua * (y2 - y1);

    return [x, y];
};

const lerp = (a: number, b: number, t: number) => { return a + t * (b - a); }

const pointOnLine = (p: Vector3, p0: Vector3, p1: Vector3) => {
    let dx = p1.x - p0.x,
        dy = p1.y - p0.y,
        t = ((p.x - p0.x) * dx + (p.y - p0.y) * dy) / (dx * dx + dy * dy),
        x = lerp(p0.x, p1.x, t),
        y = lerp(p0.y, p1.y, t);
    return new Vector3(x, y);
};

const downloadJson = (collection, name = 'download',) => {
    const link = document.createElement('a');
    link.href = 'data:application/json;charset=utf-8,' + encodeURIComponent(collection);
    link.download = `${name}`;
    link.click();
};